.cat-data {
  border: 0;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  color: #444444;
}

@media (min-width: $tablet-width) {
  .cat-data {
    font-size: 20px;
    line-height: 30px;
  }
}

.cat-data__name-input {
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  color: #444444;
  margin: 0;
  padding: 0;
  padding-left: 20px;
  width: 228px;
  height: 45px;
  margin-left: 10px;
  margin-bottom: 14px;
  border: 1px solid #e7e7e7;
}

@media (min-width: $tablet-width) {
  .cat-data__name-input {
    font-size: 20px;
    line-height: 30px;
    width: 570px;
    height: 52px;
  }
}

@media (min-width: $desktop-width) {
  .cat-data__name-input {
    width: 509px;
  }
}

.cat-data__weight-input {
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  color: #444444;
  margin: 0;
  padding: 0;
  padding-left: 20px;
  width: 207px;
  height: 45px;
  margin-left: 9px;
  margin-bottom: 14px;
  border: 1px solid #e7e7e7;
}

@media (min-width: $tablet-width) {
  .cat-data__weight-input {
    font-size: 20px;
    line-height: 30px;
    width: 542px;
    height: 52px;
  }
}

@media (min-width: $desktop-width) {
  .cat-data__weight-input {
    width: 481px;
  }
}

.cat-data__weight-input::-webkit-outer-spin-button,
.cat-data__weight-input::-webkit-inner-spin-button {
  appearance: none;
}

.cat-data__age-input {
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  color: #444444;
  margin: 0;
  padding: 0;
  padding-left: 20px;
  width: 173px;
  height: 45px;
  margin-left: 10px;
  margin-bottom: 14px;
  border: 1px solid #e7e7e7;
}

@media (min-width: $tablet-width) {
  .cat-data__name-input,
  .cat-data__weight-input,
  .cat-data__age-input {
    margin-bottom: 22px;
  }
}

@media (min-width: $tablet-width) {
  .cat-data__age-input {
    font-size: 20px;
    line-height: 30px;
    width: 500px;
    height: 52px;
  }
}

@media (min-width: $desktop-width) {
  .cat-data__age-input {
    width: 439px;
  }
}

.cat-data__age-input::-webkit-outer-spin-button,
.cat-data__age-input::-webkit-inner-spin-button {
  appearance: none;
}
